  color: black;
  box-shadow: 0px 2px 20px 1px black;
  transition: all 0.5s;
  transition-timing-function: ease-in-out;
}

.display-page-body {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  min-height: 100vh;
  scroll-behavior: smooth;
  box-shadow: 0px 2px 10px 5px black;
}

@-webkit-keyframes animatedisplay {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes animatedisplay {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.display-bg {
  position: absolute;
  object-fit: cover;
  height: 100%;
  width: 100%;
  -webkit-filter: brightness(0.5) contrast(1.09) saturate(1.5);
          filter: brightness(0.5) contrast(1.09) saturate(1.5);
  -webkit-animation: animatedisplay 2s ease-in-out forwards;
          animation: animatedisplay 2s ease-in-out forwards;
}
.display-page-content {
  position: relative;
  display: flex;
  visibility: hidden;
  padding-bottom: 2rem;
  justify-content: center;
  flex-direction: column;
  flex-wrap: wrap;
  -webkit-animation: animate ease-in-out 2s forwards;
          animation: animate ease-in-out 2s forwards;
  -webkit-animation-delay: 3s;
          animation-delay: 3s;
}
@-webkit-keyframes animate {
  0% {
    -webkit-transform: scale(0, 0);
            transform: scale(0, 0);
  }
  100% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
    visibility: visible;
  }
}
@keyframes animate {
  0% {
    -webkit-transform: scale(0, 0);
            transform: scale(0, 0);
  }
  100% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
    visibility: visible;
  }
}

